<template>
  <div>
    <div class="title">
        维修记录设备列表
    </div>
    <div class="conterBox">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <el-form-item label="设备名称">
            <el-input v-model="formInline.user" placeholder="请输入设备名称"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">查询</el-button>
          </el-form-item>
        </el-form>
        <!-- 公司的设备维修的列表 -->
        
        <!-- 新增维修记录 -->
        <el-dialog title="新增维修记录" :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="维修记录内容">
              <el-input type="textarea" :rows="6" v-model="form.desc"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="Fnaddmaintainrecord()">创建</el-button>
          </div>
        </el-dialog>

        <el-table
          :data="tableData"
          border
          style="width: 100%">
          <el-table-column
            prop="date"
            label="序列号"
            width="180">
          </el-table-column>
          <el-table-column
            prop="name"
            label="设备名称">
          </el-table-column>
          <el-table-column
            label="操作">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="primary" size="small">查看详情</el-button>
                <el-button type="primary" @click="dialogFormVisible = true" size="small">新增维修记录</el-button>
              </template>
          </el-table-column>
        </el-table>
    </div>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from "vuex";
export default {
    data(){
        return{
            formInline:{
                user:''
            },
            tableData: [{//这是公司设备维修记录的列表
                date: '2016-05-02',
                name: '王小虎'
                }, {
                date: '2016-05-04',
                name: '王小虎'
                }, {
                date: '2016-05-01',
                name: '王小虎'
                }, {
                date: '2016-05-03',
                name: '王小虎'
            }],
            dialogFormVisible: false,//新增弹框标识
            form: {//form新增表单
              desc: ''
            },
            formLabelWidth: '120px'//新增表单中的bebel
        };
    },
    methods: {
      onSubmit() {//点击查询按钮
        console.log('submit!');
      },
      handleClick(i){//查看详情按钮  i是对象，表格这一行的参数
        this.$router.push('/bkIndex/bkrepair')
        console.log(i,'点击了查看详情按钮')
      },
      Fnaddmaintainrecord(){
        this.dialogFormVisible = false
        url.addmaintainrecord({
          "user_id": this.buserInfo.user_id,
          "dm_d_id": 1,//设备id
          "dm_d_detail": this.form.desc
        }).then(res=>{
          console.log(res,'-----------维修记录创建')
        })
      }
    },
    mounted(){
      this.Fnaddmaintainrecord()
    },
    computed:{
      ...mapState(['buserInfo'])
    }
}
</script>

<style lang="stylus" scoped>
.title
    font-size 20px
    color #333
    margin-bottom 20px
    margin-top 20px
    padding-left 15px
    border-left 3px solid #409EFF
.conterBox
    padding 30px
</style>


